import React, { useEffect, useState } from "react";
import Card from "../../Card/Card";
import { projectData } from "../../../projectData";
import styles from "./PortfolioSection.module.css";

import { FaArrowLeft, FaArrowRight, FaBorderAll } from "react-icons/fa";
import { DiJavascript, DiCss3, DiReact, DiNodejsSmall } from "react-icons/di";

const PortfolioSection = () => {
  const [category, setCategory] = useState("all");
  const [projects, setProjects] = useState([]);
  const [currIdx, setCurrIdx] = useState(0);

  useEffect(() => {
    if (category === "all") {
      setProjects(projectData);
    } else {
      const filteredProjects = projectData.filter(
        (project) => project.category === category
      );
      setProjects(filteredProjects);
    }
  }, [category]);

  return (
    <section
      id="portfolioSection"
      className={`sectionPadding ${styles.portfolioSection}`}
    >
      <div className={styles.portfolioHeader}>
        <div>
          <h2 className="heading">My Portfolio</h2>
        </div>

        <div className={styles.arrowDiv}>
          <span
            className={styles.arrows}
            onClick={() => setCurrIdx((prev) => (prev >= 6 ? prev - 6 : prev))}
          >
            <FaArrowLeft />
          </span>

          <span
            className={styles.arrows}
            onClick={() =>
              setCurrIdx((prev) =>
                prev + 6 < projects.length ? prev + 6 : prev
              )
            }
          >
            <FaArrowRight />
          </span>
        </div>
      </div>

      <div className={styles.portfolioCategories}>
        <button
          className={`${category === "all" ? styles.active : ""} ${styles.categoryBtn
            }`}
          onClick={() => {
            setCategory("all");
            setCurrIdx(0);
          }}
        >
          <FaBorderAll />
          All
        </button>
        <button
          className={`${category === "css" ? styles.active : ""} ${styles.categoryBtn
            }`}
          onClick={() => {
            setCategory("css");
            setCurrIdx(0);
          }}
        >
          <DiCss3 />
          Css
        </button>
        <button
          className={`${category === "javascript" ? styles.active : ""} ${styles.categoryBtn
            }`}
          onClick={() => {
            setCategory("javascript");
            setCurrIdx(0);
          }}
        >
          <DiJavascript />
          Javascript
        </button>

        <button
          className={`${category === "react" ? styles.active : ""} ${styles.categoryBtn
            }`}
          onClick={() => {
            setCategory("react");
            setCurrIdx(0);
          }}
        >
          <DiReact />
          React
        </button>
        <button
          className={`${category === "full stack" ? styles.active : ""} ${styles.categoryBtn
            }`}
          onClick={() => {
            setCategory("full stack");
            setCurrIdx(0);
          }}
        >
          <DiNodejsSmall />
          Full Stack
        </button>
      </div>

      <div className={styles.portfolioContaier}>
        {projects?.map((project, index) => {
          return index > currIdx + 5 || index < currIdx ? (
            <div key={project.id} style={{ width: "0px", height: "0px", display: "none" }}></div>
          ) : (
            <Card
              key={index}
              img={project.image}
              title={project.title}
              description={project.description ? project.description : "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Aliquid laudantium laboriosam eum adipisci mollitia voluptatum numquam dicta quasi, blanditiis dolores."}
              webLink={project.webLink}
              category={project.category}
            />
          );
        })}
      </div>
    </section>
  );
};

export default PortfolioSection;
