import React from "react";
import styles from './SkillSection.module.css';

const SKILLS = ['./img/skills/Html.png', './img/skills/CSS.webp', './img/skills/Javascript.svg', './img/skills/Tailwind.png', './img/skills/Bootstrap.svg', './img/skills/Git.svg', './img/skills/Github.svg', './img/skills/MaterialUI.svg', './img/skills/Nodejs.svg', './img/skills/React.png', './img/skills/MongoDB.svg', './img/skills/Next.svg', './img/skills/Redux.svg', './img/skills/Express.png'];

const SkillsSection = () => {
  return (
    <section
      id="skillsSection"
      className={`sectionPadding ${styles.skillSection}`}
    >
      <h2 className="heading">My Tech Stack</h2>

      <div className={styles.skillContainer}>
        {SKILLS.map((skill, index) => (
          <img
            key={index}
            src={skill}
            className={styles.skillImg}
            alt="skill img"
            loading="lazy"
          ></img>
        ))}
      </div>
    </section>
  );
};

export default SkillsSection;
