import React, { useState } from "react";
import styles from "./ContactSection.module.css";
import toast from "react-hot-toast";
import emailjs from "emailjs-com";

const ContactSection = () => {
  const [contactDetails, setContactDetails] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    message: "",
  });

  const submitHandler = async (e) => {
    e.preventDefault();
    if (!contactDetails.firstName || !contactDetails.lastName || !contactDetails.email || !contactDetails.phone || !contactDetails.message) {
      toast.error("Please fill all the fields...", {
        duration: 800,
        style: {
          fontSize: "2rem"
        }
      });
    } else {

      try {
        // toast.loading("Loading");
        const res = emailjs.send('default_service', 'template_2vxvtt6', contactDetails, "3gElKBFu_giF-ecdJ");

        toast.promise(res, {
          loading: 'Loading',
          success: 'Your record has been submitted successfully...',
          error: 'Error when fetching',
        }, {
          style: {
            minWidth: '250px',
            fontSize: '1.6rem'
          },
          success: {
            duration: 1000,
          },
        });
        setContactDetails({
          firstName: "",
          lastName: "",
          email: "",
          phone: "",
          message: "",
        });
      } catch (error) {
        setContactDetails({
          firstName: "",
          lastName: "",
          email: "",
          phone: "",
          message: "",
        });
      }
    }
  }

  const changeHandler = (e) => {
    setContactDetails((prev) => {
      return { ...prev, [e.target.name]: e.target.value }
    });
  }

  
  return (
    <section
      id="contactSection"
      className={`${styles.contactSection} sectionPadding`}
    >
      <h2 className="heading">Contact Me</h2>
      <form className={styles.contactForm} onSubmit={submitHandler}>
        <div className={styles.flexInput}>
          <div>
            <label htmlFor="fName">First Name</label>
            <input type="text" placeholder="John" id="fName" name="firstName" value={contactDetails.firstName} onChange={changeHandler} />
          </div>
          <div>
            <label htmlFor="lName">Last Name</label>
            <input type="text" placeholder="Doe" id="lName" name="lastName" value={contactDetails.lastName} onChange={changeHandler} />
          </div>
        </div>

        <div className={styles.flexInput}>
          <div>
            <label htmlFor="email">Email</label>
            <input
              type="text"
              placeholder="example@ex.com"
              id="email"
              name="email"
              value={contactDetails.email} onChange={changeHandler} />
          </div>
          <div>
            <label htmlFor="phone">Phone Number</label>
            <input
              type="number"
              placeholder="9265812375"
              id="phone"
              name="phone"
              value={contactDetails.phone} onChange={changeHandler} />
          </div>
        </div>

        <div className={styles.textAreaDiv}>
          <label htmlFor="message">Message</label>
          <textarea name="message" id="message" placeholder="Type your message..." value={contactDetails.message} onChange={changeHandler}></textarea>
        </div>

        <div>
          <button className="btn btn-primary" type="submit">Submit</button>
        </div>
      </form>
    </section>
  );
};

export default ContactSection;
