import React from "react";
import { NavLink } from "react-router-dom";
import { FiExternalLink } from "react-icons/fi";
import { FaCode } from "react-icons/fa";
import styles from "./Card.module.css";

const Card = ({ title, img, description, codeLink, webLink, category }) => {
  return (
    <div className={styles.card}>
      <img src={img} className={styles.cardImg} alt="CardImage" loading="lazy" />
      <div className={styles.cardBody}>
        <div>
          <h4 className={styles.cardTitle}>{title}</h4>
          <p className={styles.cardCategory}>{category}</p>
        </div>
        <p className={styles.cardDescription}>{description}</p>
        <div className={styles.cardButtons}>
          <NavLink
            to={codeLink}
            className={`btn btn-primary ${styles.cardBtn}`}
          >
            Code
            <FaCode />
          </NavLink>
          <NavLink
            target="_blank"
            to={webLink}
            className={`btn btn-primary-outline ${styles.cardBtn}`}
          >
            Live
            <FiExternalLink />
          </NavLink>
        </div>
      </div>
    </div>
  );
};

export default Card;
