import React from "react";
import styles from "./HeroSection.module.css";
import {Link} from "react-scroll";

const HeroSection = () => {
  return (
    <section
      id="heroSection"
      className={`${styles.heroSection} sectionPadding`}
    >
      <div className={styles.sectionLeft}>
        <h3 className={styles.subHeading}>Hey, I'm Dheeraj</h3>
        <h2 className={styles.heading}>Full Stack Developer</h2>
        <div>
          <p className={styles.heroPara}>
            My expertise includes full stack web development, using MERN Stack
            technology. Additionally, I possess strong problem-solving skills
            and am a quick learner.
          </p>
        </div>
          <Link
            smooth={true}
            offset={-50}
            duration={500}
            className="btn btn-primary"
            to="contactSection"
          >
            Get In Touch
          </Link>
      </div>
      <div className={styles.sectionRight}>
        <img
          src="./img/hero.webp"
          className={styles.heroImg}
          alt="Web Vectors By Vecteezy"
          loading="lazy"
        />
      </div>
    </section>
  );
};

export default HeroSection;
